.App {
  text-align: center;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

input{
  padding: 5px 15px;
  font-size: 20px;
  border-radius: 4px;
  outline: 0;
  border: 0;
}

button{
  padding: 5px 15px;
  color: #fff;
  background: transparent;
  border: 1px solid #fff;
  font-size: 20px;
  margin-top: 10px;
  border-radius: 2px;
}

button:hover{
  cursor: pointer;
}
